.chart_box {
    position: relative;
    width: 100%;
    margin-top: 10px;
    overflow: hidden;
    border: outset 1px #3d4042d1;
    text-align: center;
}

.chart_box .chart_box-overlay {
    background: rgba(0, 0, 0, 0.7);
    position: absolute;
    height: 99%;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
    transition: all 0.4s ease-in-out 0s
}

.chart_box:hover .chart_box-overlay {
    opacity: 1
}

